import { mapGetters } from 'vuex'
import Footer from '~/components/Footer'
import Header from '~/components/Header'

export default {
  components: {
    Footer,
    Header,
  },
  data() {
    return {
      isMobile: null,
    }
  },
  mounted() {
    this.isMobile = window.innerWidth < 1000
    window.addEventListener('resize', () => {
      this.isMobile = window.innerWidth < 1000
    })
  },
  computed: {
    ...mapGetters({
      theme: 'common/getTheme',
    }),
  },
}
