import NavbarBrand from './NavbarBrand'
import { mapGetters } from 'vuex'
import NotificationCard from '~/components/NotificationCard'
import IconLogout from '@/components/Icons/IconLogout.vue'
import jwt_decode from 'jwt-decode'

export default {
  components: {
    NavbarBrand,
    NotificationCard,
    IconLogout,
  },
  props: {
    loginlayout: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isMobile: null,
      isNavbarOpen: false,
      visible: false,
      userDrawer: false,
      langDrawer: false,
      rootSubmenuKeys: ['sub1', 'sub2', 'sub3', 'sub4'],
      openKeys: ['sub1'],
      cst: '',
      kycVerified: 'unverified',
      productsIconsUrl: '../assets/images/homepage/product/icons/',
      submenuItems: [
        { text: 'Nakit', link: '/cash' },
        { text: 'Hızlı Takas', link: '/swap/BTC-ETH' },
        { text: 'Kredi Kartı', link: 'buy-crypto' },
        { text: 'OTC', link: '/otc' },
        { text: 'P2P', link: '/P2PMarket' },
        { text: 'Kurumsal', link: '/account/corporate' },
        { text: 'Altın', link: 'https://cointral.store/', extern: true },
        {
          text: 'Gayrimenkul',
          link: 'https://realty.cointral.com/',
          extern: true,
        },
      ],
    }
  },
  mounted() {
    const token = this.$cookies.get('Token')
    if (token) {
      const { cst } = jwt_decode(token)
      const cstArr = cst.split('@')
      const cstName = cst.substring(0, 2) + '***'
      this.cst = cstName + '@' + cstArr[1]

      const kycStatus = this.$cookies.get('KYCVerified')
      this.kycVerified = kycStatus === true ? 'verified' : 'unverified'
    }

    this.isMobile = window.innerWidth < 1000

    window.addEventListener('resize', () => {
      this.isMobile = window.innerWidth < 1000
    })
  },
  watch: {
    $route(to, from) {
      this.visible = false
      this.userDrawer = false
    },
  },
  computed: {
    ...mapGetters({ isLoggedIn: 'auth/isLoggedIn' }),
    availableLocales() {
      return this.$i18n.locales
    },
  },
  methods: {
    async switchLanguage(code) {
      await this.$i18n.setLocale(code)
    },
    onOpenChange(openKeys) {
      const latestOpenKey = openKeys.find(
        (key) => this.openKeys.indexOf(key) === -1
      )
      if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
        this.openKeys = openKeys
      } else {
        this.openKeys = latestOpenKey ? [latestOpenKey] : []
      }
    },

    logOut() {
      this.userDrawer = false
      const { $store, $cookies, $router } = this
      $cookies.removeAll()
      $store.commit('auth/setLoggedIn', false)
      $router.push(this.localeRoute('/login'))
    },

    userMenuClicked() {
      key ? (this.userDrawer = false) : null
      switch (key) {
        case '1':
          this.pushRoutePath('/wallet')
          break
        default:
          break
      }
    },

    langDrawerClicked() {
      key ? (this.langDrawer = false) : null
      switch (key) {
        case '1':
          break
        default:
          break
      }
    },

    afterVisibleChange(val) {},

    showDrawer() {
      this.visible = true
    },

    onClose() {
      this.visible = false
    },

    showUserDrawer() {
      this.userDrawer = true
    },

    onCloseUserDrawer() {
      this.userDrawer = false
    },

    closeLangDrawer() {
      this.langDrawer = false
    },
  },
}
